/* Navbar.css */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--navbar-bg);
  color: var(--text-color);
  padding: 0.9375rem 1.25rem;
  display: none;  /* Initially hidden */
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease;
  z-index: 1000;
  box-sizing: border-box;
}

.navbar ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  flex: 1;
  max-width: 100%;
}

.navbar ul li {
  margin: 0 0.9375rem;
}

.navbar ul li a {
  color: var(--text-color);
  text-decoration: none;
  font-size: 1.125rem;
  transition: color 0.3s ease;
  white-space: nowrap;
}

.navbar ul li a:hover {
  color: var(--secondary-color);
}

.navbar .theme-toggle {
  position: absolute;
  top: 50%;
  right: 1.25rem;
  transform: translateY(-50%);
  background: var(--navbar-bg);
  border: 2px solid var(--text-color);
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.25rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease, border-color 0.3s ease, transform 0.2s ease;
  color: var(--text-color);
  /* Prevent shape changes */
  min-width: 2.5rem;
  min-height: 2.5rem;
  max-width: 2.5rem;
  max-height: 2.5rem;
  padding: 0;
  margin: 0;
  box-shadow: 0 0.125rem 0.5rem rgba(0, 0, 0, 0.2);
  z-index: 1001;
  box-sizing: border-box;
}

.navbar .theme-toggle:hover {
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
  transform: translateY(-50%) scale(1.1);
  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.3);
}

.navbar .theme-toggle:active {
  transform: translateY(-50%) scale(0.95);
}

/* Tablet and smaller screens */
@media (max-width: 768px) {
  .navbar {
    padding: 0.75rem 1rem;
  }
  
  .navbar ul li {
    margin: 0 0.625rem;
  }
  
  .navbar ul li a {
    font-size: 1rem;
  }
  
  .navbar .theme-toggle {
    right: 1rem;
  }
}

/* Mobile devices */
@media (max-width: 600px) {
  .navbar {
    padding: 0.625rem 0.75rem;
  }
  
  .navbar ul li {
    margin: 0 0.5rem;
  }
  
  .navbar ul li a {
    font-size: 0.875rem;
  }
  
  .navbar .theme-toggle {
    width: 2.25rem;
    height: 2.25rem;
    font-size: 1.125rem;
    right: 0.75rem;
    min-width: 2.25rem;
    min-height: 2.25rem;
    max-width: 2.25rem;
    max-height: 2.25rem;
  }
}

/* Very small screens */
@media (max-width: 400px) {
  .navbar ul li {
    margin: 0 0.375rem;
  }
  
  .navbar ul li a {
    font-size: 0.8125rem;
  }
  
  .navbar .theme-toggle {
    width: 2rem;
    height: 2rem;
    font-size: 1rem;
    right: 0.625rem;
    min-width: 2rem;
    min-height: 2rem;
    max-width: 2rem;
    max-height: 2rem;
  }
}
