.skills-wrapper {
    padding: 2rem 1rem;
    max-width: 1280px;
    margin: auto;
    color: var(--text-color-dark);
    transition: color 0.3s ease;
}

.skills-title {
    text-align: center;
    color: var(--secondary-color, #ffffff);
    font-size: 2rem;
    font-weight: bold;
}

.skills {
    display: grid;
    gap: 2rem;
    grid-template-columns: 1fr;
    /* default: 1 column */
}

/* Responsive grid: 3 columns on wider screens */
@media (min-width: 768px) {
    .skills {
        grid-template-columns: repeat(3, 1fr);
    }
}

.skill-section {
    border-radius: 0.75rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease;
}

.skill-category {
    font-size: 1.25rem;
    color: var(--secondary-color);
    font-weight: 600;
    margin: 0;
}

.skill-list {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.skill-item {
    color: var(--text-color-dark);
    font-size: 0.85rem;
    font-weight: 500;
    white-space: nowrap;
    transition: background-color 0.2s ease;
}

.skill-item:hover {
    border-color: var(--secondary-color);
}

.skill-item {
  padding: 0.3125rem 0.875rem;
  background: var(--surface-color);
  border-radius: 0.875rem;
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 0.4375rem;
  margin-right: 0.25rem;
  display: inline-block;
  border: 1px solid var(--border-color);
  transition: background-color 0.3s ease, border-color 0.3s ease;
}


.skills-note {
  font-size: 1rem;
  font-style: italic;
  color: var(--text-color-dark);
  opacity: 0.7;
  transition: color 0.3s ease;
}


.skills-toggle-btn {
  color: var(--secondary-color);
  background: none;
  border: none;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 1rem;
  display: block;
  text-align: right; 
  margin-top: 0.5rem;
  padding: 0;
  margin-right: 3.125rem;


}


.skills-toggle-btn:focus,
.skills-toggle-btn:hover {
  color: var(--secondary-color);
  text-decoration: underline;
}



