.floating-theme-toggle {
  position: fixed;
  top: 1.25rem;
  right: 1.25rem;
  background: var(--navbar-bg);
  border: 2px solid var(--text-color);
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.25rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease, border-color 0.3s ease, transform 0.2s ease;
  color: var(--text-color);
  z-index: 1000;
  box-shadow: 0 0.125rem 0.5rem rgba(0, 0, 0, 0.2);
  /* Prevent shape changes */
  min-width: 2.5rem;
  min-height: 2.5rem;
  max-width: 2.5rem;
  max-height: 2.5rem;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.floating-theme-toggle:hover {
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
  transform: scale(1.1);
  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.3);
}

.floating-theme-toggle:active {
  transform: scale(0.95);
}

/* Tablet and smaller screens */
@media (max-width: 768px) {
  .floating-theme-toggle {
    top: 1rem;
    right: 1rem;
  }
}

/* Mobile devices */
@media (max-width: 600px) {
  .floating-theme-toggle {
    width: 2.25rem;
    height: 2.25rem;
    font-size: 1.125rem;
    top: 0.75rem;
    right: 0.75rem;
    min-width: 2.25rem;
    min-height: 2.25rem;
    max-width: 2.25rem;
    max-height: 2.25rem;
  }
}

/* Very small screens */
@media (max-width: 400px) {
  .floating-theme-toggle {
    width: 2rem;
    height: 2rem;
    font-size: 1rem;
    top: 0.625rem;
    right: 0.625rem;
    min-width: 2rem;
    min-height: 2rem;
    max-width: 2rem;
    max-height: 2rem;
  }
}

